import React, { Component } from 'react';
import AnswerBox from "./Answer";
import SubmitButton from "./Submit";
import { FormControl, FormLabel, RadioGroup, FormControlLabel } from "@material-ui/core";
import Paper from '@material-ui/core/Paper';

class AnsweredQuestionCard extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this)
        this.state = {
          value: this.props.question.answers[this.props.question.answeredIndex]
        }
    }

    render() {
      return (
        <Paper className={"question-card" + (this.props.question.isCorrect() ? " correct" : " incorrect")}>
          {this.props.question.text}
          <FormControl component="fieldset">
            <RadioGroup name="answers" value={this.state.value}>
            {this.props.question.answers.map(
                (a, i) => <AnswerBox value={String(i)} answer={a}/>
            )}
            </RadioGroup>
            <SubmitButton/>
          </FormControl>
          {(this.props.question.isCorrect() ? 
          "Correct!" : 
          "Incorrect! Correct answer: " + this.props.question.answers[this.props.question.correctAnswer])}
        </Paper>
      );
    }
}

export default AnsweredQuestionCard;