import React, { Component } from 'react';
import { FormControlLabel, Radio } from "@material-ui/core";

class AnswerBox extends Component {
    render() {
      return (
        <FormControlLabel 
            control={<Radio />} 
            label={this.props.answer} 
            value={this.props.answer}
        />
      );
    }
}

export default AnswerBox;