class Question {
    constructor(text, answers, correctAnswer) {
        this.text = text
        this.correctAnswer = correctAnswer
        this.answers = answers
        this.answeredIndex = undefined
    }

    addAnswer(answer) {
        this.answers.push(answer)
    }

    changeCorrectAnswer(correctAnswer) {
        this.correctAnswer = correctAnswer
    }

    answer(index) {
        this.answeredIndex = index
    }

    isAnswered() {
        return this.answeredIndex !== undefined
    }

    isCorrect() {
        return this.correctAnswer === this.answeredIndex
    }
}

export default Question