import React, { Component } from 'react';
import AnswerBox from "./Answer";
import SubmitButton from "./Submit";
import { FormControl, FormLabel, RadioGroup, FormControlLabel } from "@material-ui/core";
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Question from './Question';
import Paper from '@material-ui/core/Paper';

class QuestionCard extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.state = {
          value: (
            this.props.question.isAnswered() 
              ? this.props.question.answers[this.props.question.answeredIndex] 
              : this.props.question.answers[0]
          ),
          isAnswered: this.props.question.answeredIndex !== undefined 
        }
    }

    componentDidUpdate(prevProps) {
      if (this.props.question !== prevProps.question) {
        this.setState({
          value: (
            this.props.question.isAnswered() 
              ? this.props.question.answers[this.props.question.answeredIndex] 
              : this.props.question.answers[0]
          ),
          isAnswered: this.props.question.answeredIndex !== undefined 
        })
      }
    }

    onSubmit() {
      let i = this.props.question.answers.findIndex(value => value === this.state.value)
      this.props.question.answer(i)
      this.setState({
        isAnswered: true
      })
    }

    handleChange(event) {
      this.setState({
        value: event.target.value
      })
    }

    render() {
      return (
        <div className={"question-card"}>
          {this.props.question.text}
          <FormControl component="fieldset" disabled={this.props.question.isAnswered()}>
            <RadioGroup name="answers" value={this.state.value} onChange={this.handleChange}>
            {this.props.question.answers.map(
                (a, i) => <AnswerBox value={String(i)} answer={a}/>
            )}
            </RadioGroup>
          </FormControl>
          
          {
            (this.props.question.isAnswered())
              ? 
                <div>
                  {
                    (this.props.showNext
                      ? <Button onClick={() => { this.props.onNext() }} class="waves-effect waves-light btn">Next</Button>
                      : <></>
                    )
                  }
                  <p>
                    {
                      (this.props.question.isCorrect()
                        ? "Correct!"
                        : "Incorrect! Correct answer: " + this.props.question.answers[this.props.question.correctAnswer])
                    }
                  </p>
                </div>
              : 
                <div>
                  <SubmitButton callback={() => { this.onSubmit() }}/>
                </div>
          }
          
        </div>
      );
    }
}

export default QuestionCard;